.intro{
    display: flex;
    height: 80vh;
    margin-top: 5rem;
}
.ileft{
    display: flex;
    position: relative;
    flex: 1;
    flex-direction: column;
    gap: 2rem;
}
.iname{
    display: flex;
    flex-direction: column;
}
.iname>:nth-child(1)
{
    color: var(--black);
    font-weight: bold;
    font-size: 2rem;
}
.iname>:nth-child(2)
{
    color: var(--orange);
    font-weight: bold;
    font-size: 2rem;
}
.iname>:nth-child(3)
{
    font-weight: 100;
    font-size: 15px;
    color: var(--gray);
    margin-top: 9px;

}
.ibutton{
    width: 8rem;
    height: 2rem;
    

}
.iicons{
    margin-top: 0rem;
    display: flex;
    gap: 0rem;
    
}
.iicons>*{
    transform: scale(0.5);
}
.iicons>*:hover{
    cursor: pointer;
}
.iright{
    flex: 1;
    position: relative;
}
.iright>*{
    position: absolute;
    z-index: 1;
}
.iright>:nth-child(1)
{
    transform: scale(1.3);
    left:30%;
}

