.service{
    padding: 0 3rem 3rem;
    display: flex;
    height: 90vh;
    margin-top: 4rem;
    padding-top: 3rem;
}
.work{
    display: flex;
    flex-direction: column;
    position: relative;
}
.work > :nth-child(1)
{
    color: var(--orange);
    font-size: 2.5rem;
    font-weight: bold;
}
.work > :nth-child(2)
{
    color: var(--gray);
    font-size: 14px;
    margin-top: 2rem;
}
.sbutton{
    width: 10rem;
    height: 3rem;
    margin-top: 2rem;
}
.cards{
    position: relative;
}
.cards>*{
    position: absolute;
}
.sblur1{
    top:13rem;
    left:-18rem;
}
.sblur2{
    left: 15rem;
    top:10rem;
}
