.card{
     width: 15rem;
     height: 10rem;
     position: absolute;
     display: flex;
     flex-direction: column;
     gap: 1rem;
     align-items: center;
     width: 15rem;
     text-align: center;
     background: rgba(255, 255, 255, 0.25);
     border: 7px solid var(--orangecard);
     box-shadow: var(--boxShadow);
     border-radius: 20px;
     padding: 0px 26px 2rem 26px;

}
.card span:nth-of-type(1)
{
    font-weight: bold;
}
.card span:nth-of-type(2)
{
    color: var(--gray);
    font-size: 10px;
    font-weight: bold;
}
.card>img{
    transform:scale(0.6);
    margin-bottom: -2rem;
}
.cbutton{
     background: #FFF;
     box-shadow: 0px 19px 60px rgba(0,0,0,0.08);
     border-radius: 7px;
     border: none;
     padding: 10px;
     font-size: 16px;
     color: #5290FD;
     cursor: pointer;
}